import React, { useRef, useEffect } from 'react';
import { TextField, useTheme, useMediaQuery } from '@material-ui/core';
import { SquareFrame } from '../Frame';
import { TextNotesContainer, TextNotesTextFieldWrapper } from './style';

const TextNotes = () => {
    const containerRef = useRef<HTMLDivElement | null>(null);
    const theme = useTheme();
    const small = useMediaQuery(theme.breakpoints.down('sm'));

    useEffect(() => {
        const { current: container } = containerRef;
        if (container && small) {
            container.scrollIntoView();
        }
    }, [containerRef, small]);

    return (
        <TextNotesContainer ref={containerRef}>
            <TextNotesTextFieldWrapper>
                <TextField multiline rows="5" style={{ width: '100%' }} />
            </TextNotesTextFieldWrapper>
            <SquareFrame />
        </TextNotesContainer>
    );
};

export default TextNotes;
